$small: 576px;
$medium: 768px;
$large: 992px;

$active-color: #285192;
$muted-color: #f7eded;

a:hover {
    text-decoration: none;
}

.plant-side-img {
    width: 75px;
    .img {
        margin: 4px 0 4px 0;
        border-radius: 4px;
        cursor: pointer;

        img {
            width: 100%;
            height: 75px;
            aspect-ratio: "3/2";
            object-fit: "contain";
        }
    }

    @media screen and (max-width: $medium) {
        display: none;
    }

    @media screen and (min-width: $medium) {
        display: block;
    }
}

.active, .not-active {
    border-radius: 6px;
    margin: 0 5px;
    cursor: pointer;
    padding: 0;

    a {
        width: 100%;
    }
}

.active {
    background-color: $active-color;
    // margin-right: 5px;
    color: $muted-color;
    
    a:hover {
        background-color: #130101;
        color: $muted-color;
    }
}

.not-active {
    background-color: $muted-color;
    // margin-left: 5px;
    color: $active-color;
}

.login-social-link {
    color: $active-color;
    
    

    i {
        cursor: pointer;
        transition: 0.6s;
    }

    i:hover {
        color: #130101;
    }
}

.form-login {
    background-color: #fafafa;

    input {
        width: 100%;
        padding: 8px 16px;
        border-radius: 6px;
        margin: 5px 0;
    }

    button {
        width: 100%;
        background-color: $active-color;
        transition: 0.3s;
    }

    button:hover {
        background-color: #130101;
        color: $muted-color;
    }
}